.uiux__navebar{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 2rem 6rem;
}
.uiux__navebar-links{
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}
.uiux__navebar-links-logo{
    margin-right: 2rem;
}
.uiux__navebar-links-logo img{
    width: 62.56px;
    height: 16.02px;
}
.uiux__navebar-links_container{
    display: flex;
    flex-direction: row;

}
.uiux__navebar-sign{
    display: flex;
    flex-direction: row;
    align-items: center;
}
.uiux__navebar-links_container p,
.uiux__navebar-sign p,
.uiux__navebar-menu_container p{
    color: #fff;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 18px;
    line-height: 25px;
    text-transform: capitalize;
    margin: 0 1rem;
    cursor: pointer;
}
.uiux__navebar-sign button,
.uiux__navebar-menu_container button{
    padding: 0.5rem 1rem;
    color: #fff;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 18px;
    line-height: 25px;
    background: #ff4820;
    border-radius: 5px;
    border: none;
    outline: none;
    cursor: pointer;
}
.uiux__navebar-menu{
    margin-left: 1rem;
    display: none;
    position: relative;
}
.uiux__navebar-menu svg{
    cursor: pointer;
}
.uiux__navebar-menu_container{
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    flex-direction: column;
    text-align: end;
    background: var(--color-footer);
    padding: 2rem;
    position: absolute;
    top: 40px;
    right: 0;
    margin-top: 1rem;
    min-width: 210px;
    border-radius: 5px;
    box-shadow: 0 0 5 rgba(0, 0, 0, 0.2);

}
.uiux__navebar-menu_container p{
    margin: 1rem 0;
}
.uiux__navebar-menu_container-links-sign{
    display: none;
}

@media screen and (max-width: 1050px) {
    .uiux__navebar-links_container{
        display: none;
    }
    .uiux__navebar-menu{
        display: flex;
    }
}
@media screen and (max-width: 700px) {
    .uiux__navebar{
        padding: 2rem 4rem;
    }
}
@media screen and (max-width: 550px) {
    .uiux__navebar{
        padding: 2rem ;
    }
    .uiux__navebar-sign{
        display: none;
    }
    .uiux__navebar-menu_container{
        top: 20px;
    }
    .uiux__navebar-menu_container-links-sign{
        display: block;
    }
}