@import url(https://fonts.googleapis.com/css2?family=Manrope:wght@200;300;400;500;600;700;800&display=swap);
.uiux__blog-container_article{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    background: var(--color-footer);
}
.uiux__blog-container_article-image{
    width: 100%;
    height: 100%;
    background: var(--color-footer);
}
.uiux__blog-container_article-image img{
    width: 100%;
    height: 100%;
}
.uiux__blog-container_article-content{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 1rem 0.5rem;
    height: 100%;
}
.uiux__blog-container_article-content p{
    font-size: 11.649px;
    line-height: 35px;
    font-family: var(--font-family);
    font-weight: bold;
    color: #fff;
}
.uiux__blog-container_article-content h3{
    font-size: 25.11px;
    line-height: 30px;
    font-family: var(--font-family);
    font-weight: 800;
    color: #fff;
    margin-bottom: 5rem;
    cursor: pointer;
}
.uiux__blog-container_article-content p:last-child{
    cursor: pointer;
}

@media screen and (max-width: 550px) {
    .uiux__blog-container_article-content h3{
        font-size: 18px;
        line-height: 25p;
    }
}
.uiux__brand{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
}
.uiux__brand div{
    flex: 1 1;
    max-width: 150px;
    min-width: 120px;
    margin: 1rem;
    display: flex;
    justify-content: center;
    align-items: center;
}
.uiux__cta{
    border: 1px solid #fff;
    color: #fff;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;
    padding: 2rem;
    margin: 4rem;
    border-radius: 1rem;
    background: var(--gradient-bar);
}
.uiux__cta-content p{
    font-family: var(--font-family);
    font-size: 12px;
    line-height: 30px;
    font-weight: 500;
    color: #0e0e0e;
}

.uiux__cta-content h3 {
    font-family: var(--font-family);
    font-size: 24px;
    line-height: 45px;
    font-weight: 800;
    color: #000000;
}
.uiux__cta-btn{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 2rem;
}

.uiux__cta-btn button{
    background: #000000;
    border-radius: 40px;
    font-family: var(--font-family);
    font-size: 18px;
    font-weight: bold;
    line-height: 30px;
    font-weight: 500;
    color: #fff;
    padding: 0.5rem 1rem;
    border: none;
    outline: none;
    cursor: pointer;
    min-width: 150px;
}
@media screen and (max-width: 650px) {
    .uiux__cta {
        flex-direction: column;
    }

    .uiux__cta-btn {
        margin: 2rem 0 0;
    }
}

@media screen and (max-width: 550px) {
    .uiux__cta {
        flex-direction: column;
        margin: 4rem 2rem;
    }

    .uiux__cta-content h3 {
        font-size: 18px;
        line-height: 32px;
    }

    .uiux__cta-btn button {
        font-size: 14px;
        line-height: 28px;
    }
}
.uiux__features-container__feature{
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-direction: row;
    margin: 1rem;
}
.uiux__features-container__feature-title{
    flex: 1 1;
    max-width: 180;
    margin-right: 2rem;
}
.uiux__features-container__feature-title h1{
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 18px;
    line-height: 24px;
    color: #fff;
}
.uiux__features-container__feature-title div{
    width: 38px;
    height: 3px;
    
    background:var(--gradient-bar) ;
    box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
    margin-bottom: 0.25rem;
}
.uiux__features-container__feature-text{
    flex: 2 1;
    max-width: 390px;
    display: flex;
}
.uiux__features-container__feature-text p{
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 30px;
    color:var(--color-text);
}

@media screen and (max-width: 550px) {
    .uiux__features-container__feature-title h1{
        font-size: 14px;
        line-height: 22px;
    }
    .uiux__features-container__feature-title p{
        font-size: 12px;
        line-height: 20px;
    }
    .uiux__features-container__feature{
        margin: 1rem 0;
    }

    
}


.uiux__navebar{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 2rem 6rem;
}
.uiux__navebar-links{
    flex: 1 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}
.uiux__navebar-links-logo{
    margin-right: 2rem;
}
.uiux__navebar-links-logo img{
    width: 62.56px;
    height: 16.02px;
}
.uiux__navebar-links_container{
    display: flex;
    flex-direction: row;

}
.uiux__navebar-sign{
    display: flex;
    flex-direction: row;
    align-items: center;
}
.uiux__navebar-links_container p,
.uiux__navebar-sign p,
.uiux__navebar-menu_container p{
    color: #fff;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 18px;
    line-height: 25px;
    text-transform: capitalize;
    margin: 0 1rem;
    cursor: pointer;
}
.uiux__navebar-sign button,
.uiux__navebar-menu_container button{
    padding: 0.5rem 1rem;
    color: #fff;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 18px;
    line-height: 25px;
    background: #ff4820;
    border-radius: 5px;
    border: none;
    outline: none;
    cursor: pointer;
}
.uiux__navebar-menu{
    margin-left: 1rem;
    display: none;
    position: relative;
}
.uiux__navebar-menu svg{
    cursor: pointer;
}
.uiux__navebar-menu_container{
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    flex-direction: column;
    text-align: end;
    background: var(--color-footer);
    padding: 2rem;
    position: absolute;
    top: 40px;
    right: 0;
    margin-top: 1rem;
    min-width: 210px;
    border-radius: 5px;
    box-shadow: 0 0 5 rgba(0, 0, 0, 0.2);

}
.uiux__navebar-menu_container p{
    margin: 1rem 0;
}
.uiux__navebar-menu_container-links-sign{
    display: none;
}

@media screen and (max-width: 1050px) {
    .uiux__navebar-links_container{
        display: none;
    }
    .uiux__navebar-menu{
        display: flex;
    }
}
@media screen and (max-width: 700px) {
    .uiux__navebar{
        padding: 2rem 4rem;
    }
}
@media screen and (max-width: 550px) {
    .uiux__navebar{
        padding: 2rem ;
    }
    .uiux__navebar-sign{
        display: none;
    }
    .uiux__navebar-menu_container{
        top: 20px;
    }
    .uiux__navebar-menu_container-links-sign{
        display: block;
    }
}
.uiux__blog{
    display: flex;
    flex-direction: column;

}
.uiux__blog-heading{
    width: 100%;
    text-align: left;
    margin-bottom: 5rem;
}

.uiux__blog-heading h1{
    font-family: var(--font-family);
    font-size: 62px;
    font-weight: 800;
    line-height: 75px;
    letter-spacing: -0.04em;
}
.uiux__blog-container{
    display: flex;
    flex-direction: row;
}
.uiux__blog-container_groupA{
    flex: 0.75 1;
    margin-right: 2rem;
}
.uiux__blog-container_groupB{
    flex: 1 1;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 2rem;
}


@media screen and (max-width: 990px) {
    .uiux__blog-container {
        flex-direction: column-reverse;
    }


    .uiux__blog-container_groupA{
        margin: 2rem 0;
    }

    .uiux__blog-container_groupA .uiux__blog-container_article  {
       width: 48%;
    }

    .uiux__blog-container_groupA .uiux__blog-container_article-image {
        height: 250px;
    }
}

@media screen and (max-width: 700px) {
    .uiux__blog-container_groupB {
        grid-template-columns: repeat(1, 1fr);
    }

    .uiux__blog-container_groupA .uiux__blog-container_article  {
       width: 100%;
    }

    .uiux__blog-heading h1 {
        font-size: 46px;
        line-height: 52px;
    }
}

@media screen and (max-width: 550px) {
    .uiux__blog-heading h1{
        font-size: 34px;
        line-height: 42px;
    }

}
.uiux__features{
    width: 100%;
    display: flex;
    justify-content: space-between;
    flex-direction: row;
}
.uiux__features-heading{
    flex: 1 1;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-direction: column;
    text-align: left;
    margin-right: 5rem;
}
.uiux__features-heading h1{
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 34px;
    line-height: 45px;
}
.uiux__features-heading p{
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 30px;
    color: #ff8a71;
    margin-top: 2rem;
}
.uiux__features-container{
    flex: 1.5 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: column;
}


@media screen and (max-width: 990px) {
    .uiux__features{
        flex-direction: column;
    }
    .uiux__features-heading{
        margin: 0 0 2rem 0;
    }
}
@media screen and (max-width: 550px) {
    .uiux__features-heading h1{
        font-size: 28px; 
        line-height: 38px;   
    }
}
.uiux__footer{
    border: 1px solid #fff;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: var(--color-footer);

}
.uiux__footer-heading{
    width: 100%;
    text-align: center;
    margin-bottom: 3rem;
}
.uiux__footer-heading h1{
    font-family: var(--font-family);
    font-size: 62px;
    font-weight: 800;
    line-height: 75px;
    text-align: center;
    letter-spacing: -0.04em;
}
.uiux__footer-btn{
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 1rem;
    border: 1px solid #fff;
    text-align: center;
    margin-bottom: 10rem;
    cursor: pointer;
}
.uiux__footer-btn p{
    color: #fff;
    box-sizing: border-box;
    font-family: var(--font-family);
    font-size: 18px;
    line-height: 21px;
    word-spacing: 2px;
    
}
.uiux__footer-links{
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap: wrap;
    flex-direction: row;
    width: 100%;
    text-align: left;
}
.uiux__footer-links div{
    width: 250px;
    margin: 1rem;
}
.uiux__footer-links_logo{
    display: flex;
    flex-direction: column;

}
.uiux__footer-links_logo img{
    width: 180px;
    height: 30px;
    margin-bottom: 1rem;
}
.uiux__footer-links_logo p{
    font-family: var(--font-family);
    font-size: 12px;
    line-height: 14px;
    color: #fff;
}
.uiux__footer-links_div{
    display: flex;
    justify-content: flex-start;
    flex-direction: column;
}
.uiux__footer-links_div h4{
    font-family: var(--font-family);
    font-size: 14px;
    list-style: 16px;
    color: #fff;
    margin-bottom: 1rem;
}
.uiux__footer-links_div p{
    font-family: var(--font-family);
    font-size: 14px;
    list-style: 16px;
    color: #fff;
    margin: 0.5rem 0rem;
    cursor: pointer;
}
.uiux__footer-copyright{
    margin-top: 2rem;
    text-align: center;
    width: 100%;
}
.uiux__footer-copyright p{
    font-family: var(--font-family);
    font-size: 12px;
    list-style: 14px;
    font-feature-settings: 'liga' off;
    color: #fff;
    cursor: pointer;
}
@media screen and (max-width: 850px) {
    .uiux__footer-heading h1 {
        font-size: 44px;
        line-height: 50px;
    }
}

@media screen and (max-width: 550px) {
    .uiux__footer-heading h1 {
        font-size: 34px;
        line-height: 42px;
    }

    .uiux__footer-links div {
        margin: 1rem 0;
    }

    .uiux__footer-btn p {
        font-size: 14px;
        line-height: 20px;
    }
}

@media screen and (max-width: 400px) {
    .uiux__footer-heading h1 {
        font-size: 27px;
        line-height: 38px;
    }
}

.uiux__header{
    display: flex;
    flex-direction: row;
}
.uiux__header-content{
    flex: 1 1;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
    margin-right: 5rem;
}
.uiux__header-content h1{
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 62px;
    line-height: 75px;

    letter-spacing: -0.04em;
}
.uiux__header-content p{
    font-family: var(--font-family);
    font-size: 20px;
    line-height: 27px;
    color: #81AFDD;
    margin-top: 1.5rem;
}
.uiux__header-content__input{
    width: 100%;
    margin: 2rem 0 1rem;
    display: flex;

}
.uiux__header-content__input input{
    flex: 2 1;
    width: 100%;
    min-height: 50px;
    background: var(--color-footer);
    font-family: var(--font-family);
    font-size: 20px;
    line-height: 27px;
    color: #fff;
    border: 2px solid var(--color-footer);
    padding: 0 1rem;
    outline: none;
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
}
.uiux__header-content__input button{
    flex: 0.6 1;
    width: 100%;
    min-height: 50px;
    background: #ff4820;
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
    font-family: var(--font-family);
    font-weight: bold;
    font-size: 20px;
    line-height: 27px;
    color: #fff;
    cursor: pointer;
    outline: none;
    padding: 0 1rem;
    border: none;
}
.uiux__header-content__people{
    width: 100%;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin-top: 2rem;

}
.uiux__header-content__people img{
    width: 181.79px;
    height: 38px;
}
.uiux__header-content__people p{
    margin:0 0 0 1rem;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 12px;
    line-height: 38px;
    color: #fff;
    text-align: center;

}
.uiux__header-image{
    flex: 1 1;
    display: flex;
    justify-content: center;
    align-items: center;
}
.uiux__header-image img{
    width: 100%;
    height: 100%;

}


@media screen and (max-width: 1050px) {
    .uiux__header{
        flex-direction: column;
    }
    .uiux__header-content{
        margin: 0 0 3rem;
    }

}
@media screen and (max-width: 650px) {
    .uiux__header h1{
       font-size: 48px;
       line-height: 60px;
    }
    .uiux__header p{
        font-size: 16px;
        line-height: 24px;
    }
    .uiux__header-content__people{
        flex-direction: column;
    }
    .uiux__header-content__people p{
        margin: 0;
    }
    .uiux__header-content__input input,
    .uiux__header-content__input button{
        font-size: 16px;
        line-height: 24px;
    }
}
@media screen and (max-width: 490px) {
    .uiux__header h1{
        font-size: 36px;
        line-height: 48px;
    }
    .uiux__header p{
         font-size: 14px;
         line-height: 24px;
    }
    .uiux__header-content__input input,
    .uiux__header-content__input button{
        font-size: 12px;
        line-height: 16px;
    }
}
.uiux__possiblity{
    display: flex;
    flex-direction: row;
}
.uiux__possiblity-image{
    flex: 1 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin-right: 2rem;
}
.uiux__possiblity-image img{
    width: 100%;
    height: 100%;
}
.uiux__possiblity-content{
    flex: 1 1;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: flex-start;
    margin: 1rem 0;
}
.uiux__possiblity-content h4{
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 30px;
    color: #71e5ff;
}
.uiux__possiblity-content h4:last-child{
    color: #ff8a71;
}
.uiux__possiblity-content h1{
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 34px;
    line-height: 45px;
}
.uiux__possiblity-content p{
    font-family: var(--font-family);
    font-size: 16px;
    line-height: 30px;
    color: #81afdd;
}

@media screen and (max-width: 950px) {
    .uiux__possiblity{
        flex-direction: column;
    }
    .uiux__possiblity-image{
        margin: 1rem 0;
    }
    .uiux__possiblity-content{
        margin-top: 2rem;
    }

}

