.uiux__features-container__feature{
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-direction: row;
    margin: 1rem;
}
.uiux__features-container__feature-title{
    flex: 1;
    max-width: 180;
    margin-right: 2rem;
}
.uiux__features-container__feature-title h1{
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 18px;
    line-height: 24px;
    color: #fff;
}
.uiux__features-container__feature-title div{
    width: 38px;
    height: 3px;
    
    background:var(--gradient-bar) ;
    box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
    margin-bottom: 0.25rem;
}
.uiux__features-container__feature-text{
    flex: 2;
    max-width: 390px;
    display: flex;
}
.uiux__features-container__feature-text p{
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 30px;
    color:var(--color-text);
}

@media screen and (max-width: 550px) {
    .uiux__features-container__feature-title h1{
        font-size: 14px;
        line-height: 22px;
    }
    .uiux__features-container__feature-title p{
        font-size: 12px;
        line-height: 20px;
    }
    .uiux__features-container__feature{
        margin: 1rem 0;
    }

    
}

