.uiux__footer{
    border: 1px solid #fff;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: var(--color-footer);

}
.uiux__footer-heading{
    width: 100%;
    text-align: center;
    margin-bottom: 3rem;
}
.uiux__footer-heading h1{
    font-family: var(--font-family);
    font-size: 62px;
    font-weight: 800;
    line-height: 75px;
    text-align: center;
    letter-spacing: -0.04em;
}
.uiux__footer-btn{
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 1rem;
    border: 1px solid #fff;
    text-align: center;
    margin-bottom: 10rem;
    cursor: pointer;
}
.uiux__footer-btn p{
    color: #fff;
    box-sizing: border-box;
    font-family: var(--font-family);
    font-size: 18px;
    line-height: 21px;
    word-spacing: 2px;
    
}
.uiux__footer-links{
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap: wrap;
    flex-direction: row;
    width: 100%;
    text-align: left;
}
.uiux__footer-links div{
    width: 250px;
    margin: 1rem;
}
.uiux__footer-links_logo{
    display: flex;
    flex-direction: column;

}
.uiux__footer-links_logo img{
    width: 180px;
    height: 30px;
    margin-bottom: 1rem;
}
.uiux__footer-links_logo p{
    font-family: var(--font-family);
    font-size: 12px;
    line-height: 14px;
    color: #fff;
}
.uiux__footer-links_div{
    display: flex;
    justify-content: flex-start;
    flex-direction: column;
}
.uiux__footer-links_div h4{
    font-family: var(--font-family);
    font-size: 14px;
    list-style: 16px;
    color: #fff;
    margin-bottom: 1rem;
}
.uiux__footer-links_div p{
    font-family: var(--font-family);
    font-size: 14px;
    list-style: 16px;
    color: #fff;
    margin: 0.5rem 0rem;
    cursor: pointer;
}
.uiux__footer-copyright{
    margin-top: 2rem;
    text-align: center;
    width: 100%;
}
.uiux__footer-copyright p{
    font-family: var(--font-family);
    font-size: 12px;
    list-style: 14px;
    font-feature-settings: 'liga' off;
    color: #fff;
    cursor: pointer;
}
@media screen and (max-width: 850px) {
    .uiux__footer-heading h1 {
        font-size: 44px;
        line-height: 50px;
    }
}

@media screen and (max-width: 550px) {
    .uiux__footer-heading h1 {
        font-size: 34px;
        line-height: 42px;
    }

    .uiux__footer-links div {
        margin: 1rem 0;
    }

    .uiux__footer-btn p {
        font-size: 14px;
        line-height: 20px;
    }
}

@media screen and (max-width: 400px) {
    .uiux__footer-heading h1 {
        font-size: 27px;
        line-height: 38px;
    }
}
