.uiux__blog{
    display: flex;
    flex-direction: column;

}
.uiux__blog-heading{
    width: 100%;
    text-align: left;
    margin-bottom: 5rem;
}

.uiux__blog-heading h1{
    font-family: var(--font-family);
    font-size: 62px;
    font-weight: 800;
    line-height: 75px;
    letter-spacing: -0.04em;
}
.uiux__blog-container{
    display: flex;
    flex-direction: row;
}
.uiux__blog-container_groupA{
    flex: 0.75;
    margin-right: 2rem;
}
.uiux__blog-container_groupB{
    flex: 1;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 2rem;
}


@media screen and (max-width: 990px) {
    .uiux__blog-container {
        flex-direction: column-reverse;
    }


    .uiux__blog-container_groupA{
        margin: 2rem 0;
    }

    .uiux__blog-container_groupA .uiux__blog-container_article  {
       width: 48%;
    }

    .uiux__blog-container_groupA .uiux__blog-container_article-image {
        height: 250px;
    }
}

@media screen and (max-width: 700px) {
    .uiux__blog-container_groupB {
        grid-template-columns: repeat(1, 1fr);
    }

    .uiux__blog-container_groupA .uiux__blog-container_article  {
       width: 100%;
    }

    .uiux__blog-heading h1 {
        font-size: 46px;
        line-height: 52px;
    }
}

@media screen and (max-width: 550px) {
    .uiux__blog-heading h1{
        font-size: 34px;
        line-height: 42px;
    }

}