.uiux__possiblity{
    display: flex;
    flex-direction: row;
}
.uiux__possiblity-image{
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin-right: 2rem;
}
.uiux__possiblity-image img{
    width: 100%;
    height: 100%;
}
.uiux__possiblity-content{
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: flex-start;
    margin: 1rem 0;
}
.uiux__possiblity-content h4{
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 30px;
    color: #71e5ff;
}
.uiux__possiblity-content h4:last-child{
    color: #ff8a71;
}
.uiux__possiblity-content h1{
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 34px;
    line-height: 45px;
}
.uiux__possiblity-content p{
    font-family: var(--font-family);
    font-size: 16px;
    line-height: 30px;
    color: #81afdd;
}

@media screen and (max-width: 950px) {
    .uiux__possiblity{
        flex-direction: column;
    }
    .uiux__possiblity-image{
        margin: 1rem 0;
    }
    .uiux__possiblity-content{
        margin-top: 2rem;
    }

}
